import React from "react";
import { graphql } from "gatsby"


import Layout from "../components/layout"
import SEO from "../components/seo"
//import { rhythm } from "../utils/typography"

class AboutMe extends React.Component {
    render() {
        const { data } = this.props
        const siteTitle = data.site.siteMetadata.title
        const social = data.site.siteMetadata.social 

        return (
            <Layout location={this.props.location} title={siteTitle}>
            <SEO title="About" />
            <h1>About Me</h1>
            <p>
                Hi, I'm Jordan! I am a software engineer currently in Austin. I write about
                software engineering concepts, personal finance and philosophy/psychology.
            </p>
            <p></p>
            <p>
                After being unhappy with my life situation, I made a decision to change it 
                using a combination of education and financial independence moves. This website
                is going to be a combination of personal diary detailing my transition into this
                new phase of life.
            </p>
            <p>
                If you enjoy personal finance, computer science or philosophy and psychology 
                feel free to follow along or reach out to me via social.
            </p>
            <center>
              <a href={`https://linkedin.com/in/${social.linkedin}`}>
                LinkedIn
              </a>
              &#160;|&#160;
              <a href={`https://github.com/${social.github}`}>
                GitHub
              </a>
              &#160;|&#160;
              <a href={`https://twitter.com/${social.twitter}`}>
                Twitter
              </a>
              </center>
            </Layout>
            )
    }

}

export default AboutMe

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        social {
            twitter
            linkedin
            github
        }
      }
    }
}
    `